/* Week5.css */

.week5-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f7f7f7; /* Light gray background */
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.card {
  border: 10px solid #ffcc00; /* Yellow border */
  border-radius: 15px;
  overflow: hidden;
  background-color: #4caf50; /* Green background */
  box-shadow: 0 4px 8px rgba(16, 19, 209, 0.1);
  transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.card:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
  transform: translateY(-5px);
}

.card-body {
  padding: 15px;
}

.card-title {
  font-size: 24px; /* Larger title font size */
  margin-bottom: 10px;
  color: #ffffff; /* White text color */
}

.card-text {
  font-size: 16px; /* Larger text font size */
  margin-bottom: 10px;
  color: #eeeeee; /* Light gray text color */
}

/* Add a gradient background for a more colorful effect */
.card-gradient {
  background: linear-gradient(45deg, #ff6b6b, #8e44ad);
}